<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input
          placeholder=""
          v-model="input"
          class="input-with-select"
          @clear="ss"
          clearable
        >
          <el-button slot="append" icon="el-icon-search" @click="ss"></el-button>
        </el-input>
        <el-button type="primary" @click="addyh"></el-button>
      </div>
      <template>
        <el-table :data="list.goods" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="goods_name" label="" width="600"></el-table-column>
          <el-table-column prop="goods_price" label="" width="160"></el-table-column>
          <el-table-column prop="goods_weight" label="" width="160"></el-table-column>
          <el-table-column prop="add_time" label="" width="160">
            <template slot-scope="scope">{{scope.row.add_time|time}}</template>
          </el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="list.total"
      ></el-pagination>
      </div>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      yi: "",
      er: "",
      lj: "",
      ym: "1",
      myxs: "10",
      input: "",
      list: [],
      sfxx: [],
    }
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    handleSizeChange(val) {
      this.myxs = val;
      this.axios
        .get(this.lj + "?query=&pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ym = val;
      this.axios
        .get(this.lj + "?query=&pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    open(i) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
            console.log(i)
          console.log(this.lj+"/" + i.goods_id);
          this.axios.delete(this.lj+"/" + i.goods_id).then(res => {
            console.log(res);
          });
          this.$message({
            type: "success",
            message: "!"
          });
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.list = res.data.data;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    ss() {
        this.axios
        .get(
          this.lj +
            "?pagenum=" +
            this.ym +
            "&pagesize=" +
            this.myxs +
            "&query=" +
            this.input
        )
        .then(res => {
          console.log(res);
          this.list = res.data.data;
        });
    },
    addyh(){
        this.$router.push("/about/add")
    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  mounted() {
    this.yi = sessionStorage.getItem("yi");
    this.lj = sessionStorage.getItem("ly");
    this.er = sessionStorage.getItem("er");
    this.axios.get(this.lj + "?query=&pagenum=1&pagesize=10").then(res => {
      this.list = res.data.data;
      console.log(this.list);
    });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

