<template>
  <div class="boxx">
    <div class="box">
      <van-form>
        <van-field
          v-model="username"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name=""
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' }]"
        />
        <div style="margin: 16px;" id="footer">
          <el-button type="success" @click="onSubmit"></el-button>
          <el-button type="danger" @click="onCl"></el-button>
        </div>
      </van-form>
    </div>
    <div class="y">
      <img src="../assets/logo.png" alt />
    </div>
    <div class="ts"></div>
  </div>
</template>

<script>
export default {
  // 
  name: "home",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      username: "admin",
      password: "123456"
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    onSubmit() {
      this.axios
        .post("login", { username: this.username, password: this.password })
        .then(res => {
          // console.log(res);
          if (res.data.meta.status == 200) {
            this.$toast("");
            sessionStorage.setItem("token",res.data.data.token)
            setTimeout(() => {
              this.$router.push("/About");
            }, 2000);
          }else{
            this.$toast(res.data.meta.msg);
          }
        });
    },
    onCl() {
      (this.username = ""), (this.password = "");
      this.$toast("");
    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  mounted() {},
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

