<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input
          placeholder=""
          v-model="input"
          class="input-with-select"
          @clear="ss"
          clearable
        >
          <el-button slot="append" icon="el-icon-search" @click="ss"></el-button>
        </el-input>
        <el-button type="primary" @click="dialogFormVisible = true"></el-button>
      </div>
      <template>
        <el-table :data="huser" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="username" label="" width="160"></el-table-column>
          <el-table-column prop="email" label="" width="160"></el-table-column>
          <el-table-column prop="mobile" label="" width="160"></el-table-column>
          <el-table-column prop="role_name" label="" width="160"></el-table-column>
          <el-table-column prop="sg_state" label="" width="150">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
                @change="qqhh(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="email" label="" width="200">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="fp(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[2, 3, 4, 5]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="fy.total"
      ></el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pwd">
          <el-input v-model="ruleForm.pwd"></el-input>
        </el-form-item>
        <el-form-item label="" prop="yx">
          <el-input v-model="ruleForm.yx"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="ruleForm.tel"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="tjyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="names">
          <el-input v-model="ruleForms.names" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="yxs">
          <el-input v-model="ruleForms.yxs"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tels">
          <el-input v-model="ruleForms.tels"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false"> </el-button>
        <el-button type="primary" @click="xgyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibless">
      <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="">
          <el-input v-model="rule.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="rule.js" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="rule.xjs" placeholder="">
            <el-option v-for="(i,k) in sfxx" :key="k" :label="i.roleName" :value="i.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibless = false"> </el-button>
        <el-button type="primary" @click="fen()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      lj: "",
      arr: [],
      yi: "",
      er: "",
      sms: "",
      huser: [],
      input: "",
      fy: [],
      sssj: [],
      myxs: "2",
      ym: "1",
      cid: "",
      tid: "",
      vid: "",
      dialogFormVisible: false,
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: "",
      ruleForm: {
        name: "",
        pwd: "",
        yx: "",
        tel: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 16, message: " 3  16 ", trigger: "blur" }
        ],
        yx: [
          { required: true, message: "", trigger: "blur" },
          { min: 8, max: 25, message: " 8  25 ", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 11, message: " 3  11 ", trigger: "blur" }
        ]
      }
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    handleSizeChange(val) {
      this.myxs = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ym = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    open(i) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          console.log("roles/" + i.id);
          this.axios.delete("users/" + i.id).then(res => {
            // console.log(res);
          });
          this.$message({
            type: "success",
            message: "!"
          });
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    tjyh() {
      this.dialogFormVisible = false;
      this.axios
        .post("users", {
          username: this.ruleForm.name,
          password: this.ruleForm.pwd,
          email: this.ruleForm.yx,
          mobile: this.ruleForm.tel
        })
        .then(res => {
          // console.log(res)
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.ruleForms.names = ki.username;
      this.ruleForms.yxs = ki.email;
      this.ruleForms.tels = ki.create_time;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put("users/" + this.cid, {
          email: this.ruleForms.yxs,
          mobile: this.ruleForms.tels
        })
        .then(res => {
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    ss() {
      this.axios
        .get(
          this.lj +
            "?pagenum=" +
            this.ym +
            "&pagesize=" +
            this.myxs +
            "&query=" +
            this.input
        )
        .then(res => {
          console.log(res);
          this.huser = res.data.data.users;
          this.fy = res.data.data;
        });
    },
    qqhh(i) {
      this.axios.put("users/" + i.id + "/state/" + i.mg_state).then(res => {
        console.log(res);
      });
    },
    async fp(i) {
      console.log(i);
      this.dialogFormVisibless = true;
      this.rule.name = i.username;
      this.rule.js = i.role_name;
      this.tid = i.id;
      await this.axios.get("roles").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
    },
    fen() {
      this.axios
        .put("users/" + this.tid + "/role", { rid: this.rule.xjs })
        .then(res => {
          console.log(res);
          this.dialogFormVisibless = false;
        });
    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  async mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    await this.axios
      .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
      .then(res => {
        console.log(res);
        this.huser = res.data.data.users;
        this.fy = res.data.data;
      });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
