<!--scopedcssscopedcss-->
<!---->
<style scoped >
.el-card__body {
  padding: 20px;
  background: #fff;
}
.el-button--primary {
  margin-left: 20px;
}
.el-input {
  width: 40% !important;
}
.bg {
  width: 100%;
}
.sou {
  margin-bottom: 20px;
}
.tda {
  width: 40px;
}
.el-pagination {
  margin-top: 15px;
}
.el-input-group {
  width: 40%;
}
</style>
