<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <el-button type="primary" @click="dialogFormVisible = true"></el-button>
      <!-- <template>
        <el-table :data="list" stripe style="width: 100%" border>
          <el-table-column label width="60">
              <template slot-scope="scope">
              <el-switch
                v-model="scope.row.mg_state"
                active-color="#409EFF"
                inactive-color="#DCDFE6"
                @change="qqhh(scope.row)"
              ></el-switch>
            </template>
          </el-table-column>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="roleName" label="" width="330"></el-table-column>
          <el-table-column prop="roleDesc" label="" width="330"></el-table-column>
          <el-table-column prop="email" label="" width="330">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="fp(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>-->
      <template>
        <el-table :data="list" style="width: 100%" border>
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-row
                :class="['bdbotton',k==0?'bdtop':'','jzdq']"
                v-for="(i,k) in scope.row.children"
                :key="i.id"
              >
                <el-col :span="5">
                  <el-tag closable @close="bdsc(scope.row,i.id)">{{i.authName}}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    :class="[kk===0?'':'bdtop','jzdq']"
                    v-for="(ii,kk) in i.children"
                    :key="ii.id"
                  >
                    <el-col :span="6">
                      <el-tag
                        type="success"
                        closable
                        @close="bdsc(scope.row,ii.id)"
                      >{{ii.authName}}</el-tag>
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        v-for="(iii) in ii.children"
                        :key="iii.id"
                        type="warning"
                        closable
                        @close="bdsc(scope.row,iii.id)"
                      >{{iii.authName}}</el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <el-table-column label="#" type="index" width="80"></el-table-column>
          <el-table-column label="" prop="roleName" width="325"></el-table-column>
          <el-table-column label="" prop="roleDesc" width="325"></el-table-column>
          <el-table-column label="" width="325">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="xgyhff(scope.row)"
              ></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="fp(scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pwd">
          <el-input v-model="ruleForm.pwd"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="tjyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="names">
          <el-input v-model="ruleForms.names"></el-input>
        </el-form-item>
        <el-form-item label="" prop="yxs">
          <el-input v-model="ruleForms.yxs"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false"> </el-button>
        <el-button type="primary" @click="xgyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibless" @close="qk">
      <el-tree
        :data="sfxx"
        :props="defaultProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="arr"
        ref="treeRef"
      ></el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibless = false"> </el-button>
        <el-button type="primary" @click="fen()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      arr: [],
      roId:'',
      yi: "",
      er: "",
      lj: "",
      list: [],
      dialogFormVisible: false,
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: [],
      ruleForm: {
        name: "",
        pwd: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 16, message: " 3  16 ", trigger: "blur" }
        ]
      },
      defaultProps: {
        children: "children",
        label: "authName"
      }
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.ruleForms.names = ki.roleName;
      this.ruleForms.yxs = ki.roleDesc;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put(this.lj + "/" + this.cid, {
          roleName: this.ruleForms.names,
          roleDesc: this.ruleForms.yxs
        })
        .then(res => {
          this.axios.get(this.lj).then(res => {
            console.log(res);
            this.list = res.data.data;
            console.log(this.list);
          });
        });
    },
    open(i) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          console.log(this.lj + "/" + i.id);
          this.axios.delete(this.lj + "/" + i.id).then(res => {
            // console.log(res);
          });
          this.$message({
            type: "success",
            message: "!"
          });
          this.axios.get(this.lj).then(res => {
            console.log(res);
            this.list = res.data.data;
            console.log(this.list);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    async fp(i) {
      this.roId=i.id;
      console.log(i);
      await this.axios.get("rights/tree").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
      this.qx(i, this.arr);
      this.dialogFormVisibless = true;
    },
    qk() {
      this.arr = [];
      location.reload();
    },
    fen() {
      console.log(this.arr)
      const keys=[
        ...this.$refs.treeRef.getCheckedKeys(),...this.$refs.treeRef.getHalfCheckedKeys(),
      ]
      const idStr=keys.join(",")
      this.axios.post("roles/"+this.roId+"/rights",{rids:idStr}).then((res)=>{
        console.log(res)
        this.dialogFormVisibless = false;
      })
    },
    qx(i, arr) {
      console.log(i)
      if (!i.children) {
        return this.arr.push(i.id);
      }
      i.children.forEach(item => {
        this.qx(item, this.arr);
      });
    },
    tjyh() {
      this.dialogFormVisible = false;
      this.axios
        .post(this.lj, {
          roleName: this.ruleForm.name,
          roleDesc: this.ruleForm.pwd
        })
        .then(res => {
          // console.log(res)
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.list = res.data.data;
            });
        });
    },
    async bdsc(aa, b) {
      console.log(aa, b);
      const a = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      ).catch(err => err);
      if (a != "confirm") {
        return this.$message.info("");
      }
      await this.axios.delete("roles/" + aa.id + "/rights/" + b).then(res => {
        aa.children = res.data.data;
      });
    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    this.axios.get(this.lj).then(res => {
      console.log(res);
      this.list = res.data.data;
      console.log(this.list);
    });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

