<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-input placeholder="" v-model="input" class="input-with-select" clearable disabled>
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      <template>
        <el-table :data="huser" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="order_number" label="" width="220"></el-table-column>
          <el-table-column prop="order_price" label="" width="145"></el-table-column>
          <el-table-column prop="pay_status" label="" width="145">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.pay_status==0" type="danger" effect="dark"></el-tag>
              <el-tag v-if="scope.row.pay_status==1" effect="dark"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label="" width="145"></el-table-column>
          <el-table-column prop="update_time" label="" width="169">
            <template slot-scope="scope">{{scope.row.update_time|time}}</template>
          </el-table-column>
          <el-table-column prop="email" label="" width="200">
            <template slot-scope="scope">
              <el-tooltip placement="top" :enterable="false">
                <div slot="content"></div>
                <el-button
                  type="primary"
                  icon="el-icon-edit"
                  size="mini"
                  @click="xgyhff(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip placement="top" :enterable="false">
                <div slot="content"></div>
                <el-button
                  type="success"
                  icon="el-icon-location"
                  size="mini"
                  @click="fp(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 50, 100]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="fy.total"
      ></el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="/" prop="region">
          <el-select v-model="ruleForms.regions" placeholder="" @click="ssq()">
            <el-option label="" value="shanghai"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForms.names"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false"> </el-button>
        <el-button type="primary" @click="xgyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibless">
      <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-steps direction="vertical" :active="1">
          <el-step v-for="(i,k) in sfxx" :key="k" :title="i.context"></el-step>
        </el-steps>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      yi: "",
      er: "",
      input: "",
      xxxx: "10",
      ssss: "1",
      huser: [],
      fy: [],
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: [],
      ruleForm: {
        name: "",
        pwd: "",
        yx: "",
        tel: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: "",
        regions: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 16, message: " 3  16 ", trigger: "blur" }
        ],
        yx: [
          { required: true, message: "", trigger: "blur" },
          { min: 8, max: 25, message: " 8  25 ", trigger: "blur" }
        ],
        tel: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 11, message: " 3  11 ", trigger: "blur" }
        ]
      }
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    handleSizeChange(val) {
      this.xxxx = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
        .then(res => {
          //   console.log(res);
          this.huser = res.data.data.goods;
          this.fy = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ssss = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
        .then(res => {
          console.log(res);
          this.huser = res.data.data.goods;
          this.fy = res.data.data;
        });
    },
    async fp(i) {
      await this.axios.get("kuaidi/1106975712662").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
      this.dialogFormVisibless = true;
    },
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put("users/" + this.cid, {
          email: this.ruleForms.yxs,
          mobile: this.ruleForms.tels
        })
        .then(res => {
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.users;
              this.fy = res.data.data;
            });
        });
    },
    ssq(){

    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    this.axios
      .get("orders?pagenum=" + this.ssss + "&pagesize=" + this.xxxx)
      .then(res => {
        console.log(res);
        this.huser = res.data.data.goods;
        this.fy = res.data.data;
        console.log(this.huser);
      });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

