<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="el-card__body">
      <div class="sou">
        <el-button type="primary" @click="tjfl"></el-button>
      </div>
      <template>
        <!-- <el-table :data="huser" stripe style="width: 100%" border>
          <el-table-column type="index" label="#" width="60"></el-table-column>
          <el-table-column prop="cat_name" label="" width="300"></el-table-column>
          <el-table-column prop="email" label="" width="300"></el-table-column>
          <el-table-column prop="mobile" label="" width="300"></el-table-column>
          <el-table-column prop="email" label="" width="300">
            <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)"></el-button>
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
              <el-button type="warning" icon="el-icon-setting" size="mini" @click="fp(scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>-->
        <tree-table
          :data="huser"
          :columns="columns"
          :selection-type="false"
          :expand-type="false"
          show-index
          index-text="#"
          border
          :show-row-hover="false"
        >
          <template slot="isok" slot-scope="scope">
            <i class="el-icon-error" style="color:red" v-if="scope.row.cat_delete===false"></i>
            <i class="el-icon-success" v-else style="color:lightgreen"></i>
          </template>
          <template slot="order" slot-scope="scope">
            <el-tag size="mini" v-if="scope.row.cat_level===0"></el-tag>
            <el-tag size="mini" type="success" v-else-if="scope.row.cat_level===0"></el-tag>
            <el-tag size="mini" type="warning" v-else></el-tag>
          </template>
          <template slot="opt" slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="xgyhff(scope.row)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="open(scope.row)"></el-button>
          </template>
        </tree-table>
      </template>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[2, 3, 4, 5]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="fy.total"
      ></el-pagination>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-cascader
          v-model="value"
          :options="options"
          :props="{ expandTrigger: 'hover' }"
          @change="handleChange"
        ></el-cascader>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="tjyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibles">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="names">
          <el-input v-model="ruleForms.names" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="yxs">
          <el-input v-model="ruleForms.yxs"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tels">
          <el-input v-model="ruleForms.tels"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibles = false"> </el-button>
        <el-button type="primary" @click="xgyh()"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogFormVisibless">
      <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="">
          <el-input v-model="rule.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="rule.js" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="rule.xjs" placeholder="">
            <el-option v-for="(i,k) in sfxx" :key="k" :label="i.roleName" :value="i.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibless = false"> </el-button>
        <el-button type="primary" @click="fen()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      columns: [
        {
          label: "",
          prop: "cat_name"
        },
        {
          label: "",
          type: "template",
          template: "isok"
        },
        {
          label: "",
          type: "template",
          template: "order"
        },
        {
          label: "",
          type: "template",
          template: "opt"
        }
      ],
      lj: "",
      arr: [],
      yi: "",
      er: "",
      sms: "",
      huser: [],
      input: "",
      fy: [],
      sssj: [],
      myxs: "5",
      ym: "1",
      cid: "",
      tid: "",
      vid: "",
      dialogFormVisible: false,
      dialogFormVisibles: false,
      dialogFormVisibless: false,
      sfxx: "",
      ruleForm: {
        name: "",
        id: 0,
        level: 0,
        pwd: ""
      },
      ruleForms: {
        names: "",
        yxs: "",
        tels: ""
      },
      rule: {
        name: "",
        js: "",
        xjs: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ]
      },
      parent: []
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {
    handleSizeChange(val) {
      this.myxs = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.result;
          this.fy = res.data.data;
        });
    },
    handleCurrentChange(val) {
      this.ym = val;
      this.axios
        .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
        .then(res => {
          // console.log(res);
          this.huser = res.data.data.result;
          this.fy = res.data.data;
        });
    },
    open(i) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          console.log("roles/" + i.id);
          this.axios.delete("users/" + i.id).then(res => {
            // console.log(res);
          });
          this.$message({
            type: "success",
            message: "!"
          });
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.result;
              this.fy = res.data.data;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    tjyh() {
      this.dialogFormVisible = false;
      this.axios
        .post("users", {
          username: this.ruleForm.name,
          password: this.ruleForm.pwd,
          email: this.ruleForm.yx,
          mobile: this.ruleForm.tel
        })
        .then(res => {
          // console.log(res)
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.result;
              this.fy = res.data.data;
            });
        });
    },
    xgyhff(ki) {
      this.dialogFormVisibles = true;
      this.ruleForms.names = ki.username;
      this.ruleForms.yxs = ki.email;
      this.ruleForms.tels = ki.create_time;
      this.cid = ki.id;
    },
    xgyh() {
      this.dialogFormVisibles = false;
      this.axios
        .put("users/" + this.cid, {
          email: this.ruleForms.yxs,
          mobile: this.ruleForms.tels
        })
        .then(res => {
          this.axios
            .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
            .then(res => {
              console.log(res);
              this.huser = res.data.data.result;
              this.fy = res.data.data;
            });
        });
    },
    ss() {
      this.axios
        .get(
          this.lj +
            "?pagenum=" +
            this.ym +
            "&pagesize=" +
            this.myxs +
            "&query=" +
            this.input
        )
        .then(res => {
          console.log(res);
          this.huser = res.data.data.result;
          this.fy = res.data.data;
        });
    },
    qqhh(i) {
      this.axios.put("users/" + i.id + "/state/" + i.mg_state).then(res => {
        console.log(res);
      });
    },
    async fp(i) {
      console.log(i);
      this.dialogFormVisibless = true;
      this.rule.name = i.username;
      this.rule.js = i.role_name;
      this.tid = i.id;
      await this.axios.get("roles").then(res => {
        console.log(res);
        this.sfxx = res.data.data;
        console.log(this.sfxx);
      });
    },
    fen() {
      this.axios
        .put("users/" + this.tid + "/role", { rid: this.rule.xjs })
        .then(res => {
          console.log(res);
          this.dialogFormVisibless = false;
        });
    },
    async hqejfl() {
      const { data: res } = await this.axios.get("categories", {
        params: { type: 2 }
      });
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.parent = res.data;
    },
    tjfl() {
      this.hqejfl();
      dialogFormVisible = true;
    }
  },
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  async mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    await this.axios
      .get(this.lj + "?pagenum=" + this.ym + "&pagesize=" + this.myxs)
      .then(res => {
        console.log(res);
        this.huser = res.data.data.result;
        this.fy = res.data.data;
      });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

<!-- Add "scoped" attribute to limit CSS to this component only -->
