<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/About' }"></el-breadcrumb-item>
      <el-breadcrumb-item>{{yi}}</el-breadcrumb-item>
      <el-breadcrumb-item>{{er}}</el-breadcrumb-item>
    </el-breadcrumb>
    <template>
      <el-table :data="list" stripe style="width: 100%" border>
        <el-table-column type="index" label="#" width="85"></el-table-column>
        <el-table-column prop="authName" label="" width="340"></el-table-column>
        <el-table-column prop="path" label="" width="340"></el-table-column>
        <el-table-column prop="level" label="" width="340">
          <template slot-scope="scope">
            <el-tag size="mini" v-if="scope.row.level==0"></el-tag>
            <el-tag size="mini" v-if="scope.row.level==1" type="success"></el-tag>
            <el-tag size="mini" v-if="scope.row.level==2" type="warning"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </template>
  </div>
</template>

<script>
export default {
  // 
  name: "",
  //  
  props: [],
  // 
  components: {},
  // 
  data() {
    return {
      yi: "",
      er: "",
      lj:'',
      list: [],
      flaga:false,
      flagb:false,
      flagc:false,
    };
  },
  // 
  computed: {},
  // 
  watch: {},
  // 
  methods: {},
  /**
   * DOM$ el
   */
  created() {},
  /**
 /**
 * el  vm.$ el 
 *  root  mounted  vm.$ el 
 */
  mounted() {
    this.lj = sessionStorage.getItem("ly");
    this.yi = sessionStorage.getItem("yi");
    this.er = sessionStorage.getItem("er");
    this.axios.get(this.lj+"/list").then(res => {
      this.list = res.data.data;
      console.log(this.list);
    });
  },
  /**
   *  DOM 
   *  DOM  DOM 
   */
  updated() {},
  /**
   * keep-alive  keep-alive 
   */
  activated() {},
  /**
   * keep-alive  keep-alive 
   */
  deactivated() {}
};
</script> 

